'use strict';
import {default as angular} from 'angular';
import {default as ConfigProvider} from './config/config-strings.js';

/**
 * Wire-up services
 */
function servicesWireup() {
    angular.module('myApp.services')
        .provider(ConfigProvider.serviceName, ConfigProvider.configProviderFactory)

}

export default servicesWireup;




