/**
 * Realizing that putting up with router states inside of multiple files could become a very difficult to navigate the
 * code, all admin routes are being defined here and here only.
 */
'use strict';

routerConfig.$inject = ['$stateProvider', '$urlRouterProvider'];
function routerConfig($stateProvider, $urlRouterProvider) {
    $urlRouterProvider.otherwise('/home');
    $stateProvider
        .state('home', {
            url    : '/home'
            , views: {
                ''      : {
                    template      : require('../modules/home/home.html')
                    , controller  : 'HomeController'
                    , controllerAs: 'homeController'
                }
                , 'menu': {
                    template    : require('../modules/home/home-menu.html')
                    , controller: [() => {
                    }]
                }
            }
        })
        .state('components', {
            abstract  : true
            , url       : '/components'
            , template: '<ui-view></ui-view>'
        })
        .state('components.contacts', {
            abstract  : true,
            url       : '/contacts',
            template  : '<ui-view></ui-view>',
            controller: ['$scope', '$state',
                ($scope, $state) => {
                    $state.go('components.contacts.add');
                }]
            , views   : {
                'menu@manage': {
                    template    : require('../modules/components/contacts/contacts-menu.html')
                    , controller: [() => {
                    }]
                }
            }
        })
        .state('components.contacts.add', {
            url    : '/add'
            , views: {
                '@manage': {
                    template      : require('../modules/components/contacts/contacts-add.html')
                    , controller  : 'AddContactsController'
                    , controllerAs: 'addContactsController'
                }
            }
        })
}

export default routerConfig;




